import React from "react";
import { Channel, Program, useEpg } from "planby";
import theme from "../helpers/theme";
import useTVChannels from "./TVChannelsContextHook";
import moment from "moment";
  
export function useEPG() {
  const {channels} = useTVChannels();
  const [lchannels, setlChannels] = React.useState<Channel[]>([]);
  const [epg, setEpg] = React.useState<Program[]>([]);
  const [isLoading, setIsLoading] = React.useState<boolean>(false);

  const channelsData = React.useMemo(() => lchannels, [lchannels]);
  const epgData = React.useMemo(() => epg, [epg]);

  const { getEpgProps, getLayoutProps } = useEpg({
    channels: channelsData,
    epg: epgData,
    dayWidth: 7200,
    sidebarWidth: 90,
    itemHeight: 100,
    isSidebar: true,
    isTimeline: true,
    isLine: false,
    startDate: moment().subtract(2, 'hours').format('YYYY-MM-DDTHH:mm:ss'),
    endDate: moment().add(12, 'hours').format('YYYY-MM-DDTHH:mm:ss'),
    isBaseTimeFormat: true,
    itemOverscan: 3,
    theme
  });

  const handleFetchResources = React.useCallback(async () => {
    setIsLoading(true);
    const newepg=channels.getEPG();
    //console.log("dumb prog2:",newepg);    
    const newchannels = channels.getChannelsPlanby();    
    setEpg(newepg as Program[]);
    setlChannels(newchannels as Channel[]);
    setIsLoading(false);
  }, [channels]);

  React.useEffect(() => {
    handleFetchResources();
  }, [handleFetchResources]);

  return { getEpgProps, getLayoutProps, isLoading };
}
