import React from "react";
import { Epg, Layout } from "planby";

// Import hooks
import { useEPG } from "./useEPG";

import Timeline from "./Timeline";
import ChannelItem from "./ChannelItem";
import Program from "./ProgramItem";
import useTVChannels from "./TVChannelsContextHook";


const EPG: React.FC = () => {
  const { isLoading, getEpgProps, getLayoutProps } = useEPG();
  const { isEPGOpen }=useTVChannels();
  if (!isEPGOpen) return (<></>);
  return (
    <div style={{ position:"absolute", top:"5vh",left:"5vw",height: "90vh", width: "90vw" }}>      
      <Epg isLoading={isLoading} {...getEpgProps()}>
        <Layout
          {...getLayoutProps()}
          renderTimeline={(props) => <Timeline {...props} />}
          renderProgram={({ program, ...rest }) => (
            <Program key={program.data.id} program={program} {...rest} />
          )}
          renderChannel={({ channel }) => (
            <ChannelItem key={channel.uuid} channel={channel} />
          )}
        />
      </Epg>
    </div>
  );
}
export default EPG;