import React, { useEffect, useRef } from "react";
import ChannelList from "../components/ChannelList";
import PlayerwSpeedials from "../components/PlayerwSpeedials";
import useTVChannels from "../components/TVChannelsContextHook";
import OverlayContent from "../components/OverlayContent";
import OSD from "../components/OSD";
import ProgramOverlay from "../components/ProgramOverlay";
import Navigation from "../components/Navigation";
import EPG from "../components/EPG";

const MainUI: React.FC = () => {
  const inactivityTimer = useRef<ReturnType<typeof setInterval> | null>(null);
  const {
    channels,
    currentChannel,
    xs,
    isChannelsListOpen,
    isUserInactive,
    isOverlayPinned,
    isVideoPaused,
    videoVolume,
    setIsUserInactive,
    setIsOverlayShowing,
    setIsVideoPaused,
    setVideoVolume,
    switchChannelById,
  } = useTVChannels();

  const handleActivity = (event: { keyCode?: number; stopPropagation?: any; }) => {
    event.stopPropagation();
    setIsUserInactive(false);
    if (inactivityTimer.current) clearTimeout(inactivityTimer.current);
    inactivityTimer.current = setTimeout(
      () => {
        setIsUserInactive(true);
      },
      process.env.NODE_ENV !== "production" ? 5000 : 5000
    ); // 5 seconds
  };
  const handleKeyPress = (event: { keyCode: number }) => {
    if (event.keyCode === 38) {
      setVideoVolume(videoVolume+0.05);
    }
    if (event.keyCode === 40) {
      setVideoVolume(videoVolume-0.05);
    }
    if (event.keyCode === 32) {
      setIsVideoPaused(!isVideoPaused);
    }
    if (event.keyCode === 37 && channels) {
      const next = channels.getPreviousChannel()?.id;
      if (next) switchChannelById(next);
    }
    if (event.keyCode === 39) {
      const next = channels.getNextChannel()?.id;
      if (next) switchChannelById(next);
    }
    handleActivity(event);
  };
  useEffect(() => {
    document.addEventListener("mousemove", handleActivity);
    document.addEventListener("mousedown", handleActivity);
    document.addEventListener("keydown", handleKeyPress);
    document.addEventListener("touchstart", handleActivity);

    return () => {
      document.removeEventListener("mousemove", handleActivity);
      document.removeEventListener("mousedown", handleActivity);
      document.removeEventListener("keydown", handleKeyPress);
      document.removeEventListener("touchstart", handleActivity);
    };
  });

  useEffect(() => {
    if (isUserInactive) {
      document.body.style.cursor = "none";
    } else {
      document.body.style.cursor = "default";
    }
    
    if (!isUserInactive && (xs || !isChannelsListOpen)) {
      setIsOverlayShowing(true);
    } else {
      if (!isOverlayPinned){
        setIsOverlayShowing(false);
      }      
    }
  }, [isUserInactive, isChannelsListOpen, xs, setIsOverlayShowing, isOverlayPinned]);

  return (
    <div
      style={{
        position: "fixed",
        width: "100vw",
        height: "100vh",
        paddingRight: isChannelsListOpen && xs ? "17.35rem" : "0",
      }}
    >     
      <OverlayContent>         
          <OSD/>
          <ProgramOverlay />
      </OverlayContent>      
      <PlayerwSpeedials url={currentChannel?.streamUrl || ""} />      
      <ChannelList />
      <EPG/>
      <Navigation/>      
    </div>
  );
};

export default MainUI;
