import * as React from 'react'
import useTVChannels from './TVChannelsContextHook';

interface OverlayContentProps {
  children?:React.ReactNode;
  noPadding?: boolean
}

const OverlayContent: React.FC<OverlayContentProps> = ({ children, noPadding }) => {
    const { currentChannel, isOverlayShowing,isEPGOpen} = useTVChannels();
    if (isEPGOpen||!isOverlayShowing ||!currentChannel) {
        return <></>;
    }
    return <div style={{ position: "fixed",
        flex: 1,
        padding:noPadding ? '0' : '1.5rem',
        width:"100%"
    }}>
        {children}    
    </div>

}

export default OverlayContent