import React, { useEffect, useState } from "react";
import ReactPlayer from "react-player";
import SpeedDial from "@mui/material/SpeedDial";
import SpeedDialAction from "@mui/material/SpeedDialAction";
import {
  PlayArrow as PlayArrowIcon,
  Pause as PauseIcon,
  SkipNext as SkipNextIcon,
  SkipPrevious as SkipPreviousIcon,
  VolumeOff as VolumeOffIcon,
  VolumeUp as VolumeUpIcon,
  Fullscreen as FullscreenIcon,
  UnfoldMoreDouble,
} from "@mui/icons-material";
import TouchEventComponent from "./TouchEventComponent";
import useTVChannels from "./TVChannelsContextHook";


interface PlayerwSpeedialsProps {
  url: string;
}

const PlayerwSpeedials: React.FC<PlayerwSpeedialsProps> = ({ url }) => {
 
  const {
    channels,
    xs,
    isChannelsListOpen,
    isUserInactive,
    isVideoMuted,
    isVideoPaused,
    videoVolume,
    setVideoVolume,    
    setIsVideoMuted,
    setIsFullScreen,
    setIsVideoPlaying,
    setIsVideoPaused,
    switchChannelById,
  } = useTVChannels();
  const [open, setOpen] = useState(false);
 
  // const [playing, setPlaying] = useState(true);
  // const [muted, setMuted] = useState(false);
  
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const switchNextChannel = () => {
    const next = channels.getNextChannel();
    if (next) switchChannelById(next.id);
  };
  const switchPreviousChannel = () => {
    const next = channels.getPreviousChannel();
    if (next) switchChannelById(next.id);
  };
  const handleFullscreenToggle = () => {
    if (!document.fullscreenElement) {
      if (document.documentElement.requestFullscreen) {
        document.documentElement.requestFullscreen().then(() => {
          setIsFullScreen(true);
        });
      }
    } else {
      if (document.exitFullscreen) {
        document.exitFullscreen().then(() => {
          setIsFullScreen(false);
        });
      }
    }
  };
  const actions = [
    {
      icon: !isVideoPaused ? (
        <PauseIcon
          sx={{ backdropFilter: "blur(5px)"}}
        />
      ) : (
        <PlayArrowIcon
          sx={{ backdropFilter: "blur(5px)" }}
        />
      ),
      name: !isVideoPaused ? "Pause" : "Play",
      action: () => setIsVideoPaused(!isVideoPaused),
    },
    {
      icon: (
        <SkipNextIcon
          sx={{ backdropFilter: "blur(5px)" }}
        />
      ),
      name: "Next",
      action: () => switchNextChannel(),
    },
    {
      icon: (
        <SkipPreviousIcon
          sx={{ backdropFilter: "blur(5px)" }}
        />
      ),
      name: "Previous",
      action: () => switchPreviousChannel(),
    },
    {
      icon: isVideoMuted ? (
        <VolumeOffIcon
          sx={{ backdropFilter: "blur(5px)"}}
        />
      ) : (
        <VolumeUpIcon
          sx={{ backdropFilter: "blur(5px)" }}
        />
      ),
      name: isVideoMuted ? "Unmute" : "Mute",
      action: () => setIsVideoMuted(!isVideoMuted),
    },
    {
      icon: (
        <FullscreenIcon
          sx={{ backdropFilter: "blur(6px)"}}
        />
      ),
      name: "Full Screen",
      action: () => handleFullscreenToggle(),
    },
  ];
  useEffect(() => {
    const onFullScreenChange = () => {
      setIsFullScreen(!!document.fullscreenElement);
    };

    document.addEventListener("fullscreenchange", onFullScreenChange);

    return () => {
      document.removeEventListener("fullscreenchange", onFullScreenChange);
    };
  }, [setIsFullScreen]);
  return (
    <div className="kitsos">
      <TouchEventComponent
        onSwipedLeft={() => switchNextChannel()}
        onSwipedRight={() => switchPreviousChannel()}
        onSwipedUp={() => setVideoVolume(videoVolume+0.1)}
        onSwipedDown={() => setVideoVolume(videoVolume-0.1)}
      >
        <ReactPlayer
          url={url}
          playing={!isVideoPaused}
          volume={videoVolume}         
          onPlay={()=>{setIsVideoPlaying(true)}}
          onPause={()=>setIsVideoPlaying(false)}
          onEnded={()=>setIsVideoPlaying(false)}
          onError={()=>{           
            setIsVideoPlaying(false);}}          
          muted={isVideoMuted}
          width="100%"
          height="100%"
          fileconfig={{ attributes: { playsinline: true } }}
          playsinline={true}
          style={{display:"grid"}}
          className='mitsos'      
        />
      </TouchEventComponent>     
      <SpeedDial
        ariaLabel="Player Controls"
        FabProps={{
          size: "small",
          color: "default",
          variant: "extended",
          sx: {},
        }}
        icon={<UnfoldMoreDouble />}
        onClose={handleClose}
        onOpen={handleOpen}
        open={open}
        direction={xs ? "left" : "up"}
        sx={{
          position: "fixed",
          bottom: xs ? 10 : 20,
          right: xs ? "0.91rem" : "0.1rem",
          ...((isChannelsListOpen || isUserInactive) && { display: "none" }),
        }}
      >
        {actions.map((action) => (
          <SpeedDialAction
            key={action.name}            
            icon={action.icon}
            tooltipTitle={action.name}
            onClick={() => {
              action.action();
              handleClose();
            }}
            sx={{
              color:"rgb(255, 255, 255)",
              backgroundColor:"#2e2d2d",
            }}
          />
        ))}
      </SpeedDial>
    </div>
  );
};

export default PlayerwSpeedials;
