import React from "react";
import { Box, IconButton, Stack, Typography } from "@mui/material";
import Progress from "./Progress";
import { calcDurationPerc, formatTime, remainingTime } from "../utils";
import CloseIcon from '@mui/icons-material/Close';
import PushPinIcon from '@mui/icons-material/PushPin';
import useTVChannels from "./TVChannelsContextHook";

const ProgramOverlay: React.FC = () => {
  
  const { currentChannel, isChannelsListOpen, isOverlayPinned, setIsOverlayShowing, setIsOverlayPinned } = useTVChannels();  

  const program = currentChannel?.currentProgram;
  const programInfo = program?.getProgramInfo();
  const programStart = program?.start;
  const programStop = program?.stop;
  return (
    <>
      {currentChannel && program?.channel && (
        <Box
          sx={{
            position: "fixed",
            bottom: "1rem",
            left: "1.7rem",
            right: isChannelsListOpen ? "18rem" : "2rem",
            height: "25vh",
            minHeight: "100px",
            backgroundColor: "#282c34a2",
            backdropFilter: "blur(2px)",
            padding: 2,
            borderRadius: 2,
            display: "flex",
            alignItems: "center",
          }}
        >
        <Stack direction="row"
        sx={{position: "fixed",top: 2,right: 2,}}>          
          <IconButton color={isOverlayPinned ? "primary":"default"} onClick={() => setIsOverlayPinned(!isOverlayPinned)}>
            <PushPinIcon fontSize="small"  />
          </IconButton>
          <IconButton onClick={() => setIsOverlayShowing(false)}>
            <CloseIcon fontSize="small"/>
          </IconButton>
        </Stack>
        
       
          <div style={{width:"100%"}}>
            <Typography variant="h5">{programInfo?.title}</Typography>
            <Progress
              value={calcDurationPerc(programStart || new Date(), programStop)}
              start={formatTime(programStart)}
              label={remainingTime(programStop)}
            />
            <div
              style={{
                overflow: "scroll",
                textOverflow: "ellipsis",
                width: "100%",
                height: "9vh",
                minHeight: "1rem",
              }}
            >
              <Typography variant="body2">{programInfo?.desc}</Typography>
            </div>
            <Typography
              variant="body2"
              style={{
                overflow: "hidden",
                width: "100%",
                textOverflow: "ellipsis",
                height: "1rem",
              }}
            >
              {programInfo?.subTitle}
            </Typography>
          </div>
        </Box>
      )}
    </>
  );
};

export default ProgramOverlay;
