import React from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import './App.css';
import TVChannels from './components/TVChannels';
import LoadingScreen from './components/LoadingScreen';
import { CssBaseline, ThemeProvider, createTheme, styled } from '@mui/material';
import useMediaQuery from '@mui/material/useMediaQuery';
import Main from './pages/Main';
import NotFoundPage from './pages/NotFound';
import About from './pages/About';

const App: React.FC = () => {
  // const theme = useTheme();
  const xs = useMediaQuery('(min-width:420px)');
  const sm = useMediaQuery('(min-width:600px)');
  const md = useMediaQuery('(min-width:1200px)');

  const GlobalStyle = styled('div')(({ theme }) => ({
    '& .MuiCssBaseline-root': {
      backgroundColor: theme.palette.background.default,
      color: theme.palette.text.primary,
    },
    'html, body, #root, #app': { // Depending on the ID of your root div
      height: '100%',
      backgroundColor: theme.palette.background.default,
    }
  }));
  

  const theme = createTheme({
    palette: {
      mode: 'dark',
      background: {
        default: '#000',  // Adjust this value as per your preference
        paper: '#050607', // Adjust this value as per your preference
      },
    },
    typography: {      
      fontSize: xs ? (md?14:12):10,
    },
    components: {
      MuiUseMediaQuery: {
        defaultProps: {
          noSsr: true,
        },
      },
    },
  });

  return (
   
    <ThemeProvider theme={theme}>
      <LoadingScreen/>
      <GlobalStyle />
      <CssBaseline />      
      <TVChannels m3u8Url="./iptv.m3u8" xs={xs} sm={sm} md={md}>
      <BrowserRouter>
        <div>          
          <Routes>
            <Route path="/tv" element={<Main/>} />
            {/* <Route path="/epg" element={<EPG/>} /> */}
            <Route path="/tv/about" element={<About/>} />
            <Route path="*" element={<NotFoundPage/>} />  
          </Routes>
        </div>
      </BrowserRouter>          
      </TVChannels>
  </ThemeProvider>  
  );
}

export default App;
