import React from "react";
import useTVChannels from "./TVChannelsContextHook";
import { Stack, IconButton } from "@mui/material";
import { LiveTv, Dvr } from "@mui/icons-material";

const Navigation: React.FC = () => {
  const {
    xs,
    sm,
    isChannelsListOpen,
    isEPGOpen,
    isUserInactive,
    setIsEPGOpen,
    setIsChannelsListOpen,
  } = useTVChannels();
  if (isChannelsListOpen || isUserInactive) return <></>;
  return (
    <Stack direction="column"
      sx={{
        position: "fixed",
        top: sm ? "calc(42vh)" : xs ? "calc(15vh)" : "calc(15vh)",
        right: xs ? 10 : 5,
        zIndex: 10,
        backdropFilter: "blur(5px)",        
      }}
    >
      <IconButton size="large"
        onClick={() => {
          setIsChannelsListOpen(!isChannelsListOpen);
        }}
      >
        <LiveTv />
      </IconButton>
      <IconButton size="large"       
        onClick={() => {          
          setIsEPGOpen(!isEPGOpen);
        }}
      >
        <Dvr />
      </IconButton>
    </Stack>
  );
};

export default Navigation;
