import Program from "./Program";
import Programmes from "./Programmes";


class Channel {
    id: string;
    name: string;
    tvgname: string;
    iconSrc: string | null;
    streamUrl: string | null;
    programmes: Programmes;
    currentProgram: Program;

    constructor(id: string, name: string,tvgname: string, iconSrc: string | null = null, streamUrl: string | null = null) {
        this.id = id;
        this.name = name;
        this.tvgname = tvgname;
        this.iconSrc = iconSrc;
        this.streamUrl = streamUrl;
        this.programmes = new Programmes();
        this.currentProgram = this.getCurrentProgram();
    }

    getCurrentProgram(): Program {
        return this.programmes.currentProgramm() || new Program();
    }

    updateCurrentProgram(): void {
        this.currentProgram = this.getCurrentProgram();
    }
    getNextProgram(): Program | null {
        return this.programmes.getNextProgram();
    }
    isCurrent(channel:Channel|null):boolean{
        if (!channel) return false;
        if (this.id===channel.id) return true;
        return true;
    }
}
export default Channel;