import React from "react";
import {
  Avatar,
  DialogContent,
  Drawer,
  IconButton,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
} from "@mui/material";
import Channel from "../classes/Channel";
import useTVChannels from "./TVChannelsContextHook";

type ChannelListProps = {
  onChannelClick?: (channel: Channel) => void;
};

const ChannelList: React.FC<ChannelListProps> = ({ onChannelClick }) => {
  const {
    channels,
    currentChannel,    
    isChannelsListOpen,
    setIsChannelsListOpen,
    switchChannelById,
  } = useTVChannels();

  return (
    <>
      <Drawer
        anchor="right"
        open={isChannelsListOpen}
        variant="temporary"
        ModalProps={{
          onClose: (ev, reason) => {
            setIsChannelsListOpen(false);
          },
        }}        
      >
        <DialogContent sx={{ paddingLeft: "0.3rem"}}>
          <List>
            {channels.getAllChannels().map((channel) => (
              <ListItem
                divider
                button
                key={channel.id}
                onClick={() => {
                  switchChannelById(channel.id);
                  if (onChannelClick) onChannelClick(channel);
                  setIsChannelsListOpen(false);
                }}
                sx={{ paddingLeft: "0.2rem" }}
                autoFocus={
                  currentChannel && currentChannel.id === channel.id
                    ? true
                    : false
                }
                selected={
                  currentChannel && currentChannel.id === channel.id
                    ? true
                    : false
                }
              >
                <ListItemAvatar sx={{ mr: "0.2rem" }}>
                  {channel.iconSrc ? (
                    <Avatar
                      variant="square"
                      src={channel.iconSrc}
                      alt={`${channel.name} Icon`}
                      sx={{ width: 64, height: 44 }}
                      imgProps={{
                        style: {
                          maxHeight: "100%",
                          maxWidth: "100%",
                          objectFit: "contain",
                        },
                      }}
                    />
                  ) : (
                    <Avatar>{channel.name.charAt(0)}</Avatar>
                  )}
                </ListItemAvatar>
                <ListItemText
                  secondaryTypographyProps={{
                    noWrap: true,
                    sx: { fontSize: 9 },
                  }}
                  primary={channel.name}
                  secondary={channel.currentProgram.getProgramInfo().title}
                  sx={{
                    maxWidth: "10.2rem",
                    width: "10.2rem",
                  }}
                />
              </ListItem>
            ))}
          </List>
        </DialogContent>
      </Drawer>
    </>
  );
};

export default ChannelList;
